import React from "react";
import './testimonial.css';
import testimonials from './store';

const TestimonialSection = () => {
    return(
        <section className="section__testimonials">
            <div className="container">
                <div className="row">
                    <div className="col-sm-12">
                        <div className="section__subtitle light__subtitle">
                            Наши студенты о <span>SolarLab academy.</span>
                        </div>
                        <h2 className="section__title light__title">
                            Отзывы
                        </h2>
                    </div>
                </div>
                <div className="row">
                    <div className="col-xs-12">
                        <div className="testimonials__icon">
                            <i className="ion-quote" aria-hidden="true"></i>
                        </div>
                    </div>
                    <div className="col-xs-12">
                        <div id="testimonials__carousel" className="owl-carousel owl-theme">
                            { testimonials.map( item => { return(
                                <div key={item.id} className="testimonials__item">
                                    <div className="testimonials_item__title">{ item.title }</div>
                                    <div className="testimonials_item__text">{ item.description }</div>
                                    <div className="testimonials_item__author"><span>{ item.author }</span></div>
                                </div>
                            )}) }
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default TestimonialSection